.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 15;
  display: flex;
  align-items: center;
  padding: var(--offset) var(--offset-s);
  transition-duration: 0.5s;
  transition-property: transform, background-color;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.nav__logo {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.7);
  margin-right: 0.5em;
  transition-duration: 0.5s;
  transition-property: opacity;
}
.nav__logo:hover, .nav__logo:focus, .nav__logo:focus-within {
  opacity: 0.6;
}
.nav__logo img {
  width: calc(var(--offset) * 10);
  height: calc(var(--offset) * 2.5);
}
.nav__list {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
  justify-content: space-between;
  margin: 0 auto;
}
.nav-lang {
  position: relative;
  margin-left: calc(var(--offset) * 0.5);
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-text-primary);
  border-radius: 50%;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color, box-shadow;
}
.nav-lang:hover, .nav-lang:focus {
  color: var(--color-text-secondary);
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.nav__register {
  margin-left: calc(var(--offset) * 0.5);
  background-color: var(--color-text-secondary);
  border-radius: 2em;
  padding: 0.5em 1em !important;
}
.nav__register:hover, .nav__register:focus {
  color: var(--color-text-secondary);
  background-color: var(--color-bg-secondary);
}
.nav__login {
  margin-left: calc(var(--offset) * 1);
}
.nav__member {
  margin-left: 0.5em;
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: 0.7em 1em !important;
  border-radius: 2em;
  font-style: italic;
  font-weight: 600;
  font-size: calc(var(--fz) * 0.8);
  letter-spacing: -0.04em;
  background: linear-gradient(130deg, #e44134 0%, #fff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: opacity 0.5s;
  min-width: fit-content;
}
.nav__member svg {
  width: calc(var(--offset) * 1.2);
  height: calc(var(--offset) * 0.8);
  fill: #EF928B;
}
.nav__member:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.12);
  transition: opacity 0.5s;
}
.nav__member:hover, .nav__member:focus, .nav__member:focus-within {
  opacity: 0.8;
}
.nav__member:hover::before, .nav__member:focus::before, .nav__member:focus-within::before {
  opacity: 0.8;
}
.nav__link {
  font-weight: 400;
  font-size: var(--fz);
  letter-spacing: -0.03em;
  color: var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: background-color, color;
  padding: calc(var(--offset) * 0.3) 0;
  min-width: fit-content;
  white-space: nowrap;
}
.nav__link.current {
  border-bottom: 1px solid var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: border-bottom, background-color, color;
}
.nav__link.current:hover, .nav__link.current:focus, .nav__link.current:focus-within {
  border-bottom: 1px solid var(--color-text-secondary);
}
.nav__link:hover, .nav__link:focus, .nav__link:focus-within {
  color: var(--color-text-secondary);
}

.btn-white {
  padding: calc(var(--offset) * 0.8);
}

.dropdown {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  position: absolute;
  z-index: 10;
  bottom: -170%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1em;
  padding: calc(var(--offset) * 0.8);
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}
.dropdown-item {
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-text-primary);
}
.dropdown-link {
  transition-duration: 0.5s;
  transition-property: color;
}
.dropdown-link:hover, .dropdown-link:focus, .dropdown-link:focus-within {
  color: var(--color-text-secondary);
}

.profile {
  display: flex;
  gap: var(--offset);
  align-items: center;
  margin: 0 calc(var(--offset) * 2);
  transition-duration: 0.5s;
  transition-property: opacity;
}
.profile:hover, .profile:focus, .profile:focus-within {
  opacity: 0.7;
}
.profile img {
  width: calc(var(--offset) * 2.5);
  height: calc(var(--offset) * 2.5);
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 0.05em var(--color-text-primary);
  object-fit: cover;
}
.profile__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.2em;
  padding: 0.5em 0;
}
.profile__info b {
  font-weight: 500;
  font-size: var(--fz);
  letter-spacing: -0.05em;
}
.profile__info span {
  font-weight: 400;
  font-size: var(--fz);
  color: #787b83;
}

.header-hide {
  transform: translateY(-100%);
}

.scroll-header {
  background-color: var(--color-bg-secondary);
}

.footer {
  background-color: var(--color-bg-secondary);
  border-top-left-radius: var(--offset);
  border-top-right-radius: var(--offset);
  overflow: hidden;
  padding: var(--offset-s);
  display: flex;
  align-items: flex-start;
  gap: var(--offset);
  margin-top: calc(var(--offset) * -2);
  position: relative;
  z-index: 1;
}
.footer-contacts {
  display: flex;
  flex-direction: column;
  gap: var(--offset);
  margin-left: auto;
}
.footer-contacts b {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.6);
  line-height: 119%;
  letter-spacing: -0.03em;
  color: var(--color-text-primary);
}
.footer-contacts a {
  max-width: 80%;
  font-weight: 600;
  font-size: calc(var(--fz) * 1.2);
  line-height: 119%;
  letter-spacing: -0.02em;
  color: var(--color-text-primary);
}
.footer-links {
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 0.8);
}
.footer-links li a {
  font-weight: 600;
  font-size: var(--fz);
  line-height: 139%;
  letter-spacing: -0.03em;
  color: var(--color-text-primary);
}
.footer-icons {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
  margin-left: auto;
}
.footer-social {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
}
.footer-social li a svg {
  width: calc(var(--offset) * 1.2);
  height: calc(var(--offset) * 1.2);
  fill: #787b83;
  transition-duration: 0.5s;
  transition-property: fill;
}
.footer-social li:hover svg, .footer-social li:focus-within svg, .footer-social li:focus svg {
  fill: var(--color-text-secondary);
}

.footer-location {
  max-width: 80%;
  font-weight: 400 !important;
  font-size: calc(var(--fz) * 0.75) !important;
  line-height: 119%;
  letter-spacing: -0.02em;
  color: var(--color-text-primary);
}

.footer-auth {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.7);
}

.footer-auth__btn {
  border-radius: 2em;
  padding: calc(var(--offset) * 0.6) calc(var(--offset) * 1.2);
  color: #fff;
  transition-property: background-color, box-shadow;
  transition-duration: 0.5s;
}

.login {
  font-weight: 400;
  font-size: calc(var(--fz) * 0.9);
  letter-spacing: -0.04em;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}
.login:hover, .login:focus, .login:focus-within {
  background-color: #fc4739;
  box-shadow: 0 0 0 0.05em #fc4739;
}

.register {
  font-weight: 700;
  background-color: #fc4739;
  box-shadow: 0 0 0 0.05em #fc4739;
}
.register:hover, .register:focus, .register:focus-within {
  background-color: inherit;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}

.footer a {
  transition-duration: 0.5s;
  transition-property: color;
}
.footer a:hover, .footer a:focus, .footer a:focus-within {
  color: #fc4739;
}

.main-head {
  width: 100%;
  min-height: 100vh;
  position: relative;
  padding: var(--offset-s);
  padding-top: var(--offset-xl);
  display: flex;
  flex-direction: column;
}
.main-head__img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.main-head__img img {
  width: 100%;
  height: 100%;
  z-index: 0;
}
.main-head__desc {
  margin-top: auto;
  position: relative;
  z-index: 1;
  margin-right: auto;
  max-width: 50%;
}
.main-head__desc h1 {
  font-weight: 600;
  font-size: calc(var(--fz) * 3);
  line-height: 1.2;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
  margin-bottom: calc(var(--offset) * 1.5);
}
.main-head__desc h1 strong {
  color: #fc4739;
}
.main-head__desc p {
  font-weight: 500;
  font-size: var(--fz);
  line-height: 128%;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
  width: 80%;
  margin-bottom: calc(var(--offset) * 1.8);
}
.main-head__link {
  padding: calc(var(--offset) * 0.3) calc(var(--offset) * 0.3) calc(var(--offset) * 0.3) calc(var(--offset) * 1.2);
  width: fit-content;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
  border-radius: 3em;
  display: flex;
  align-items: center;
  gap: 1.5em;
  transition-duration: 0.5s;
  transition-property: box-shadow;
}
.main-head__link:hover, .main-head__link:focus, .main-head__link:focus-within {
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.main-head__link:hover b, .main-head__link:focus b, .main-head__link:focus-within b {
  color: var(--color-text-secondary);
}
.main-head__link:hover span, .main-head__link:focus span, .main-head__link:focus-within span {
  background-color: var(--color-text-secondary);
}
.main-head__link:hover span svg, .main-head__link:focus span svg, .main-head__link:focus-within span svg {
  stroke: var(--color-text-primary);
}
.main-head__link b {
  font-weight: 600;
  font-size: calc(var(--fz) * 0.9);
  line-height: 1;
  letter-spacing: -0.04em;
  color: var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color;
}
.main-head__link span {
  background-color: var(--color-text-primary);
  border-radius: 50%;
  width: calc(var(--offset) * 2);
  height: calc(var(--offset) * 2);
  display: flex;
  align-items: center;
  justify-content: center;
  transition-duration: 0.5s;
  transition-property: background-color;
}
.main-head__link span svg {
  width: calc(var(--offset) * 0.8);
  height: calc(var(--offset) * 0.9);
  stroke: var(--color-icon);
  transition-duration: 0.5s;
  transition-property: stroke;
}
.main-head__logo {
  position: relative;
  z-index: 1;
  margin-left: auto;
  width: calc(var(--offset) * 25);
  height: calc(var(--offset) * 6);
}
.main-head__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.directions {
  padding: var(--offset-s);
  min-height: 100vh;
}
.directions-head {
  display: flex;
  align-items: flex-start;
  gap: var(--offset-s);
  justify-content: space-between;
  margin-bottom: calc(var(--offset) * 2);
}
.directions-head p {
  font-weight: 500;
  font-size: var(--fz);
  line-height: 128%;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
}
.directions-cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: calc(var(--offset) * 2);
}
.directions-card {
  padding: calc(var(--offset) * 2) calc(var(--offset) * 2.5);
  display: flex;
  flex-direction: column;
  gap: 1em;
  align-items: center;
  transition-duration: 0.5s;
  transition-property: background-color;
  flex: 1;
}
.directions-card__image {
  width: calc(var(--offset) * 4);
  height: calc(var(--offset) * 4);
  border-radius: 50%;
  overflow: hidden;
}
.directions-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 0.5s;
  transition-property: transform;
}
.directions-card b {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.2);
  line-height: 96%;
  letter-spacing: -0.06em;
  text-align: center;
  color: var(--color-text-primary);
}
.directions-card:hover, .directions-card:focus, .directions-card:focus-within {
  background-color: var(--color-bg-secondary);
}
.directions-card:hover img, .directions-card:focus img, .directions-card:focus-within img {
  transform: scale(1.1);
}

.title {
  font-weight: 600;
  font-size: calc(var(--fz) * 2.5);
  line-height: 1.1;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
  width: 100%;
}
.title strong {
  color: var(--color-text-secondary);
}

.advantages {
  padding: var(--offset-s);
  min-height: 80vh;
}
.advantages h3 {
  margin-bottom: calc(var(--offset) * 2);
}
.advantages-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--offset);
  margin-bottom: var(--offset-xl);
}
.advantages-card {
  background-color: var(--color-bg-secondary);
  padding: calc(var(--offset) * 2);
  border-radius: 0.5em;
  overflow: hidden;
}
.advantages-card__head {
