  padding: calc(var(--offset) * 0.5);
  padding-right: 0;
  background-color: var(--color-text-primary);
  border-radius: 1em;
  box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.2);
  width: calc(var(--offset) * 18);
  height: fit-content;
  transition-duration: 0.5s;
  transition-property: opacity;
  opacity: 0;
  z-index: -1;
}
.notifications-modal.open {
  opacity: 1;
  z-index: 10;
}
.notifications-modal .tr {
  display: block;
  margin: 0 auto;
  width: var(--offset);
  height: var(--offset);
  border-radius: 0.05em;
  margin-top: -7%;
  transform: translateX(-0.2em);
  margin-bottom: calc(var(--offset) * 0.5);
}

.notifications-modal__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--offset);
  margin-bottom: calc(var(--offset) * 0.5);
}
.notifications-modal__head .read {
  margin-left: 1em;
  position: relative;
  font-weight: 500;
  font-size: calc(var(--fz) * 0.7);
  line-height: 1.2;
  color: var(--color-text-secondary);
}
.notifications-modal__head .read:hover, .notifications-modal__head .read:focus {
  text-decoration: underline;
}
.notifications-modal__head .read.has-read {
  color: #94c898;
}
.notifications-modal__head .read.has-read::before {
  position: absolute;
  top: 20%;
  left: -15%;
  content: "";
  display: block;
  width: 0.7em;
  height: 0.5em;
  transform-origin: center;
  transform: rotate(-65deg);
  border-left: 0.05em solid #94c898;
  border-bottom: 0.05em solid #94c898;
}

.notifications-modal__btn {
  width: calc(var(--offset) * 0.9);
  height: calc(var(--offset) * 0.9);
  position: relative;
  padding: 0.5em;
  cursor: pointer;
  margin-right: calc(var(--offset) * 0.5);
  margin-top: calc(var(--offset) * 0.5);
}
.notifications-modal__btn span {
  display: block;
