  position: absolute;
  top: 0;
  left: 0;
}
.notifications-modal__btn span:nth-of-type(1) {
  transform: rotate(45deg);
}
.notifications-modal__btn span:nth-of-type(2) {
  transform: rotate(-45deg);
}

.notifications-modal__content {
  max-height: 60vh;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 0.5);
