  color: #0b1117;
  display: block;
  margin-bottom: 0.5em;
}
.notifications-modal__card b span {
  font-weight: 400;
}
.notifications-modal__card a {
  font-weight: 400;
  font-size: calc(var(--fz) * 0.7);
  line-height: 1.2;
  color: #478cc8;
  margin-bottom: 0.5em;
}
.notifications-modal__card a:hover, .notifications-modal__card a:focus {
  text-decoration: underline;
}

.notifications-modal__link {
