}
.add-field:hover svg, .add-field:focus svg {
  stroke: var(--color-text-secondary);
}
.add-field svg {
  width: calc(var(--offset) * 0.5);
  height: calc(var(--offset) * 0.5);
  stroke: #c8c8c8;
  transition-duration: 0.5s;
  transition-property: stroke;
}

.tarifs {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--offset);
  margin-top: calc(var(--offset) * 2.6);
}
.tarifs-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
  padding: var(--offset);
  border-radius: 1em;
  max-width: calc(var(--offset) * 15);
  color: #141f2a;
  transition-duration: 0.5s;
  transition-property: transform, color, background-color;
}
.tarifs-card.current {
  background-color: var(--color-text-secondary);
  color: var(--color-text-primary);
}
.tarifs-card:hover, .tarifs-card:focus {
  background-color: var(--color-text-secondary);
  color: var(--color-text-primary);
  transform: translateY(-10%);
}
.tarifs-card:hover .tarifs-card__list li span, .tarifs-card:focus .tarif-btn {
  background-color: var(--color-text-primary);
}
.tarifs-card.current .tarifs-card__list li span, .tarifs-card.current .tarif-btn {
  background-color: var(--color-text-primary);
}
.tarifs-card:hover .tarifs-card__list li span svg, .tarifs-card:focus .tarif-btn {
  fill: #141f2a;
}
.tarifs-card.current .tarifs-card__list li span svg, .tarifs-card.current .tarif-btn {
  fill: #141f2a;
}
.tarifs-card:hover .tarif-btn, .tarifs-card:focus .tarif-btn {
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}
.tarifs-card.current .tarif-btn, .tarifs-card.current .tarif-btn {
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}
.tarifs-card__count {
