  grid-template-columns: auto auto;
  gap: var(--offset);
}
.tarifs-card__count p {
  font-weight: 700;
  font-size: calc(var(--fz) * 1.5);
  line-height: 1.2;
  margin-bottom: var(--offset);
}
.tarifs-card__desc {
  display: flex;
  flex-direction: column;
  gap: 0.2em;
  width: fit-content;
  justify-self: center;
